.preview-wrapper {
  width: 480px;
  height: 560px;
  padding: 24px;
  background-color: #FFC107;
  border-radius: 4px;
  position: relative;
}

.preview-title {
  font-family: 'Lobster', cursive;
  font-size: 24px;
}

.price {
  font-size: 24px;
  font-weight: 500;
  text-align: center;
  position: absolute;
  left: 0;
  bottom: 113px;
  width: 100%;
}

.preview-list {
  margin-top: 16px;
}

.preview-single-items {
  margin-bottom: 6px;
}

.preview-items {
  position: relative;
  left: 40px;
  margin: 6px 0;
  list-style-type: circle;
}

.link {
  color:rgba(255, 255, 255, 0.5);
  text-decoration: none;
  display: block;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
